.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.m-1-pr{
  margin: 1%
}

.m-1-b{
  margin-bottom: 1%;
}

.m-10-b{
  margin-top: 10%;
}

.m-2-b{
  margin-top: 2%;
}

.small-box .icon{
  right: -15px;
}

.small-box{
  overflow: hidden;
}

.small-box .inner{
  height: 160px;
}

.small-box .inner p{
  font-size: 1.6em;
  font-weight: bold;
  position: relative;
  top: 25%;
}

body{
  background-color: #ecf0f5;
  /* overflow-y: hidden; */
}

.text-l{
  text-align: left;
}

/* card css */
.custom .card-header{
  padding: 0.2rem 1.25rem;
}

input[type=checkbox]{
  position: relative;
  top: 0.16em;
  margin-right: 1rem;
}

table .action-btn{
  margin: 0px 3px;
  float: right;
  cursor: pointer;
}

.pointer{
  cursor: pointer;
}

.full-height{
  /* height: 90vh; */
}

.pdf-view .box-body{
  padding: 0px;
}

@media (min-width: 1281px) {
  
  .pdf-view .box{
    /* height: 90vh; */
  }

  .pdf-view .box object{
    height: 84vh;
  }

  #detail-pg .setting .box-body{
    height: 84vh;
    overflow-y: scroll;
  }
}

@media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {
  .pdf-view .box{
    /* height: 85vh; */
  }

  .pdf-view .box object{
    /* height: 74vh; */
  }
  
}

.p-0{
  padding: 0px;
}

.p-5-tbl th, .p-5-tbl td{
  padding:5px;
}

.p-5-tbl th{
  background: rgba(0,0,0,.05);
}

.float-right{
  float: right;
}

#detail-pg td .fa{
  color: gray;
  zoom: 1;
  position: relative;
  top: 4px;
}

.detail-setting button{
  background: none;
  color: gray;
  border: 0px;  
}

.inputWrapper {
  height: 32px;
  width: 64px;
  overflow: hidden;
  position: relative;
  cursor: pointer;
  /*Using a background color, but you can use a background image to represent a button*/
  background-color: #DDF;
}
.fileInput {
  cursor: pointer;
  height: 100%;
  position:absolute;
  top: 0;
  right: 0;
  z-index: 99;
  /*This makes the button huge. If you want a bigger button, increase the font size*/
  font-size:50px;
  /*Opacity settings for all browsers*/
  opacity: 0;
  -moz-opacity: 0;
  filter:progid:DXImageTransform.Microsoft.Alpha(opacity=0)
}

.detail-setting .text{
  position: relative;
    top: 4px;
}

.detail-setting svg{
  float: right;
    position: relative;
    top: 5px;
    color: gray;
    zoom: 0.9;
}

.line-through{
  text-decoration: line-through;
}

.border-1-gray{
  border: 1px solid gray;
}

.hide{
  display: none;
}

.show{
  display: block;
}

.input-btn-comp svg{
  position: relative;
  top: 1px;
}

.input-btn-comp svg:hover{
  color: #ecf0f5 !important;
}

.input-btn-comp button{
  border: 1px solid gray;
}
.input-btn-comp{
  position: relative;
  top:8px !important;
}

.m-l-r-10{
  margin: 10px 0px;
}

.form-check{
  display: flex;
}

.text-data{
  white-space: pre-line;
  padding: 5px;
}

.box.box-solid.box-primary {
  border: 0px;
}

.opt-upload-btn{
  position: relative;
  top: 24px;
  margin-left: 12px;
}

.filter-element{
  float: right;
  display: inline-flex;
}

.w-240{
  width: 240px;
}

